import CommentsModel from "../Components/LayoutArea/Models/CommentModel";

export class CommentsState {
  public comments: CommentsModel[] = [];
}

export enum CommentsActionType {
  CommentsDownloaded = "CommentsDownloaded",
  CommentsAdded = "CommentsAdded",
}

export interface CommentsAction {
  type: CommentsActionType;
  payload?: any;
}

export function commentsReducer(
  currentState: CommentsState = new CommentsState(),
  action: CommentsAction
): CommentsState {
  const newCommentsState = { ...currentState }; // duplicate currentState to newState
  switch (action.type) {
    case CommentsActionType.CommentsDownloaded:
      newCommentsState.comments = action.payload;
      break;
    case CommentsActionType.CommentsAdded:
      newCommentsState.comments.push(action.payload);
      break;
  }

  return newCommentsState;
}
