import axios from "axios";
import { useForm } from "react-hook-form";
import CommentModel from "../../LayoutArea/Models/CommentModel";
import "./AddComment.css";
import { useParams } from "react-router-dom";
import { CommentsActionType } from "../../../Redux/CommentsState";
import { Globals } from "../../../Services/Global";
import store from "../../../Redux/Store";

interface ParamTypes {
  gameId: string;
}

function AddComment() {
  const { gameId } = useParams<ParamTypes>();
  const { register, handleSubmit, reset, errors } = useForm<CommentModel>();
  async function submit(comment: CommentModel) {
    try {
      const response = await axios.post<CommentModel>(Globals.commentsUrl, {
        game_id: gameId,
        comment: comment.comment,
      });
      store.dispatch({
        type: CommentsActionType.CommentsAdded,
        payload: response.data,
      });
      reset();
    } catch (err) {
      console.log(err);
    }
  }
  return (
    <div className="AddComment col-8">
      <form onSubmit={handleSubmit(submit)}>
        <textarea
          name="comment"
          className="form-control"
          placeholder="Add new comment..."
          ref={register({ required: true, minLength: 3, maxLength: 100 })}
        ></textarea>
        {errors.comment?.type === "required" && (
          <span className="badge bg-danger error">Missing Comment</span>
        )}
        {errors.comment?.type === "minLength" && (
          <span className="badge bg-danger error">Comment too short</span>
        )}
        {errors.comment?.type === "maxLength" && (
          <span className="badge bg-danger error">
            Comment exceed 100 letters
          </span>
        )}
        <br />
        <button className="btn btn-success">Add Comment</button>
      </form>
    </div>
  );
}

export default AddComment;
