import GamesModel from "../Components/LayoutArea/Models/GamesModel";

export class GamesState {
  public games: GamesModel[] = [];
}

export enum GamesActionType {
  GamesDownloaded = "GamesDownloaded",
}

//Products Action:
export interface GamesAction {
  type: GamesActionType;
  payload?: any;
}

export function gamesReducer(
  currentState: GamesState = new GamesState(),
  action: GamesAction
): GamesState {
  const newState = { ...currentState }; // duplicate currentState to newState
  switch (action.type) {
    case GamesActionType.GamesDownloaded:
      newState.games = action.payload;
      break;
  }

  return newState;
}
