import React from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import Basketball from "../../GamesArea/Basketball/Basketball";
import Football from "../../GamesArea/Football/Football";
import GameScore from "../../GamesArea/GameScore/GameScore";
import Page404 from "../Page404/Page404";

function Routing(): JSX.Element {
  return (
    <div className="Routing">
      <Switch>
        <Route path="/football" component={Football} exact />
        <Route path="/basketball" component={Basketball} exact />
        <Route path="/:category/:gameId" component={GameScore} exact />
        <Redirect from="/" to="/football" exact />
        <Route component={Page404} />
      </Switch>
    </div>
  );
}

export default Routing;
